export default {
	data(){
		return{
			address:null,
			goods:[],
			freight:6,
			totalPrice:0,
			remarks:""
		}
	},
	onShow(){
		this.loadingData();
	},
	onUnload(e){
		// console.log(e);
		
	},
	methods:{
		addAddress(){
			uni.navigateTo({
				url:"/pages/mys/address/address"
			})
		},
		loadingData(){
			this.$request.GET({
				url:this.$api.apiUrl.GET_SHOW_ORDER_COMMIT
			}).then(res=>{
				if(res.code === 200){
					if(res.data.address !== null){
					this.address = res.data.address;
					}
					this.goods = res.data.goods;
					// console.log(this.goods);
					for (const key of this.goods) {
						this.totalPrice += Number(key.goods_price) * key.goods_num
						this.freight = this.totalPrice>300 ? 0 : 6;
					}
				}else{
					uni.switchTab({
						url:'/pages/carts/cart/cart'
					})
				}
			})
		},
		placeOrder(){
			this.$request.POST({
				url:this.$api.apiUrl.POST_ORDER_COMMIT,
				data:{
					message:this.remarks
				}
			}).then(res=>{
				uni.showToast({
					title:res.msg
				})
				if(res.code === 200){
					uni.navigateTo({
						url:`/pages/mys/orderInfo/orderInfo?id=${res.data}`
					})	
				}
			})
		}
	}
}

